export default {
    props: ['time'],
    data() {
        return {
            timeLeft: undefined,
            timeTotal: undefined,
            interval: undefined
        }
    },
    computed: {
        progress() {
            if (this.timeLeft && this.timeTotal) {
                return parseInt(100 - this.timeLeft/this.timeTotal * 100);
            } else {
                return 0;
            }
        }
    },
    methods: {
        count() {
            const now = Date.now();
            const left = this.time - now;
            if (left > 0) {
                this.timeLeft = left;
                this.$emit('count', this.timeLeft);
            } else {
                this.end();
            }
        },
        abort() {
            if (this.interval) {
                this.timeTotal = undefined;
                this.timeLeft = undefined;
                window.clearInterval(this.interval);
            }
        },
        start() {
            this.abort();
            if (this.time > 0) {
                const now = Date.now();
                this.timeLeft = this.time - now;
                this.timeTotal = this.timeLeft;
                this.interval = setInterval(this.count.bind(this), 1000);
                this.count();
            }
        },
        end() {
            this.abort();
            this.$emit('timesup');
        }
    },
    watch: {
        time: function() {
            this.start();
        }
    }
}



